					'2': 'Havarijné poistenie',
					'3': 'Poistenie hypotéky',
					'4': 'Životné poistenie',
					'5': 'GAP poistenie finančnej straty',
					'6': 'Investície',
					'7': 'Cestovné poistenie',
					'8': 'Poistenie zodpovednosti zamestnanca',
					'9': 'Poistenie majetku - byt, dom, domácnosť',
					'10': 'Poistenie podnikateľov',
					'72': 'Sedadlá',
					'82': 'Test',
					'73': 'Zdravotné poistenie cudzincov',
					'85': 'Starobné dôchodkové sporenie',
					'86': 'Doplnkové dôchodkové sporenie',
				};
				return STATE_core().current_step() + ', ' + groups[id];
			},
			print_session_token()
			{
				return STATE_core().get_session_token();
			},
			print_agent_code()
			{
				let code = STATE_params().get_agent_code();
				return code === '' ? '-' : code;
