<template>
	<section class="container">
		<div class="row">

			<!-- gz -->
			<div class="col-12 text-center">
				<span class="i_check_large"></span>
				<h2 class="blue mt-3">Gratulujeme</h2>
				<p>
					Návrh zmluvy o poskytnutí finančnej služby bol úspešne vytvorený a zaslaný poskytovateľovi. <br>
					Pre to, aby začal platiť, je potrebné sledovať inštrukcie tu a v e-maili (úhrada,...).
				</p>
			</div>

			<!-- what next -->
			<div v-show="!dds" class="col-12 text-center pt-5 pb-5">
				<h2>Čo ďalej?</h2>
				<p v-html="what_next_wording"></p>
			</div>

			<!-- 1 payment -->
			<div class="col-12 step-box">
				<div class="s-box-header">
					<span class="i_next_step1"></span>
					<h3 v-show="!dds" class="blue">Uhradiť splátku</h3>
					<span v-show="!dds">Vyberte si spôsob, akým chcete vykonať úhradu</span>
				</div>
				<div v-show="!dds" class="s-box-body">
					<div class="row">
						<div class="col-12 col-md-4 mb-4">

							<!-- payment gateways -->
							<Gateways />

						</div>
						<div class="col-12 col-sm-6 col-md-4 mb-4">

							<!-- payment info -->
							<Info />

						</div>
						<div class="col-12 col-sm-6 col-md-4 mb-4">

							<!-- payment qr code -->
							<Qr />

						</div>
					</div>
				</div>
				<div v-show="dds">
					<p class="mt-3 mb-5">{{ html.view_confirm_payment }}</p>
				</div>
			</div>

			<!-- 2 documents -->
			<div class="col-12 step-box">
				<div class="s-box-header">
					<span class="i_next_step2"></span>
					<h3 class="blue">Uložiť dokumenty</h3>
					Stiahnite si dokumenty, a vytlačte čo potrebujete
				</div>

				<!-- dokumenty -->
				<Documents_prepare :start_doc_requests="start_doc_requests" @all_docs_finished="all_docs_finished" step_html="5" />

			</div>

			<!-- 3 next steps -->
			<div class="col-12 step-box">
				<div class="s-box-header">
					<span class="i_next_step3"></span>
					<h3 class="blue">E-mail a ďalšie inštrukcie</h3>
					Všetko podstatné nájdete aj vo svojej schránke
				</div>
				<div class="s-box-body">
					<div v-html="html.view_confirm_text" ></div>
				</div>
			</div>

			<!-- TODOOOOOOOOOOOOOOO -->
			<div class="col-12 px-0 mt-5">
				<div id="docs_landing_in_step5">
					<!-- <div id="osm_app" data-version="0.3.11"></div> -->
					<!-- replaced via js -->
				</div>
			</div>

		</div>
	</section>

	<!-- remap existing session data into other new session -->
	<Remap v-if="remap_active" />

</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import Documents_prepare from '@/components/form_app/documents/Documents_prepare.vue';
	import Gateways from '@/components/form_app/payment/Gateways.vue';
	import Info from '@/components/form_app/payment/Info.vue';
	import Qr from '@/components/form_app/payment/Qr.vue';
	import Session_data from '@/logic/requests/Session_data.js';
	import STATE_params from '@/store/STATE_params.js';
	import Get_form_html from '@/logic/requests/Get_form_html.js';
	import Remap from '@/components/form_app/payment/Remap.vue';

	export default {
		props: {
		},
		components: {
			Documents_prepare,
			Gateways,
			Info,
			Qr,
			Remap,
		},
		emits: [],
		data() {
			return {
				group: 0,
				receiver: 0,
				html: {
					view_confirm_payment: '',
					view_confirm_text: '',
				},
				start_doc_requests: false,
			}
		},
		created() {
			// save group id for wording filters
			this.group = STATE_core().product_group_id();
			// html templates
			Get_form_html.get_it('view_confirm_text').then(
				(response) => {
					this.html.view_confirm_text = response.result?.html;
				}
			);
			Get_form_html.get_it('view_confirm_payment').then(
				(response) => {
					this.html.view_confirm_payment = response.result?.html;
				}
			);
			// get_data for selected_product which is needed for documents
			Promise.resolve().then(
				() => {
					return Session_data.get_data();
				}
			).then(
				(response) => {
					STATE_params().set_selected_product(
						response.result?.call_params?.product_id,
						response.result?.call_params?.form_id,
						response.result?.call_params?.next_contract,
						response.result?.call_params?.period,
					);
					// save receiver for remap
					this.receiver = parseInt(response.result?.call_params?.receiver, 10);
					// start document requests
					this.start_doc_requests = true;
				}
			);
		},
		methods: {
		},
		computed: {
			what_next_wording()
			{
				if (this.group === 4/*zivotne*/) {
					return 'Splátku uhraďte <strong class="orange">čo najskôr</strong>.';
				}
				return 'Splátku uhraďte <strong class="orange">čo najskôr</strong>. Bez včasnej úhrady zmluva nevznikne.';
			},
			dds()
			{
				// dss and dds and cudzinci PGs have different html content
				return (this.group === 85/*dss*/ || this.group === 86/*dds*/ || this.group === 73/*cudzinci*/ || this.group === 3/*hypoteky*/);
			},
			remap_active()
			{
				return this.receiver === 2/*finport*/ && this.group === 1/*pzp*/;
			},
		},
		watch: {
		},
	}
</script>
