<template>
	<div
		v-if="!wp_preview_tmp"
		:class="['d-flex', (still_waiting) ? 'document_waiting' : '']"
	>

		<!-- left -->
		<div class="w-100">
			<!-- download name -->
			<button
				@click="download_pdf"
				class="pe-1"
			>
				{{ name }}
			</button>
			<!-- loading -->
			<Bouncer_loading v-show="still_waiting" size="small" />
		</div>

		<!-- right -->
		<div class="flex-shrink my-auto">
			<!-- download icon -->
			<button
				@click="download_pdf"
			>
				<span class="i_download_item"></span>
			</button>
		</div>

	</div>
</template>

<script>
	import Get_static_docs from '@/logic/requests/Get_static_docs.js';
	import Get_session_docs from '@/logic/requests/Get_session_docs.js';
	import Functions from '@/logic/Functions.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';

	export default {
		props: {
			doc: Object,
		},
		components: {
			Bouncer_loading,
		},
		data() {
			return {
				is_downloading: false,
			}
		},
		created() {
		},
		methods: {
			download_pdf() {
				if (this.still_waiting) {
					return;
				}
				this.is_downloading = true;
				if (this.is_static) {
					Get_static_docs.get_static_document(this.doc.id).then(
						(response) => {
							Functions.downloadBase64(this.name, response.result.content);
							this.is_downloading = false;
						}
					);
				} else {
					Get_session_docs.get_session_document(this.doc.id).then(
						(response) => {
							Functions.downloadBase64(this.name, response.result.content);
							this.is_downloading = false;
						}
					);
				}
			},
		},
		computed: {
			is_static() {
				return (this.doc.filename === undefined) ? false : true;
			},
			name() {
				return this.is_static ? this.doc.filename : this.doc.name;
			},
			wp_preview_tmp()
			{
				return this.name === 'ws-preview.tmp' ? true : false;
			},
			still_waiting() {
				if (this.is_downloading) {
					return true;
				}
				if (this.is_static) {
					return false;
				}
				// waiting | available | error
				if (this.doc.documentState === 'waiting') {
					return true;
				} else if (this.doc.documentState === 'available') {
					return false;
				}
				// error?
				return true;
			},
		},
	}
</script>
