/**
 * Special_input_behaviour
*/
class Special_input_behaviour
{
	/**
	 * combined_inputs
	*/
	combined_inputs(field_id, field_value)
	{
		let combinations = {
			'1': {
				names: {
					radio: 'auto_typ_2',
					select: 'auto_typ',
				},
				pairs: {
					'1': '1',
					'2': '20',
					'3': '33',
				},
			},
			'2': {
				names: {
					radio: 'havarijne_auto_typ2',
					select: 'havarijne_auto_typ',
				},
				pairs: {
					'1': '1',
					'2': '2',
					'3': '20',
				},
			},
			'7': {
				names: {
					radio: 'cestovne_uzemna_platnost',
					select: 'cestovne_krajina',
				},
				pairs: {
					'1': '51',
					'2': '210',
				},
			},
		};
		let group = State.product_group_id();
		if (!combinations.hasOwnProperty(group)) {
			return;
		}
		let radio_id = combinations[group].names.radio;
		let select_id = combinations[group].names.select;
		if (field_id === radio_id)
		{
			let select_value = STATE_params().get_user_input(select_id);
			let paired_value = combinations[group].pairs[field_value];
			if (paired_value !== undefined && select_value !== paired_value) {
				STATE_params().set_user_input(select_id, paired_value);
			}
		}
		if (field_id === select_id)
		{
			let radio_value = STATE_params().get_user_input(radio_id);
			let paired_value = Functions.find_key_by_value(combinations[group].pairs, field_value);
			if (paired_value !== undefined && radio_value !== paired_value) {
				STATE_params().set_user_input(radio_id, paired_value);
			}
		}
	}


	/**
	 * auto_model
	*/
	auto_model(field_id, label)
	{
		let combinations = {
			'1': {
				child: 'auto_model',
				parent: 'auto_znacka',
			},
			'2': {
				child: 'havarijne_auto_model',
				parent: 'havarijne_auto_znacka',
			},
			'5': {
				child: 'gap_auto_model',
				parent: 'gap_auto_znacka',
			},
			'72': {
				child: 'sedadla_auto_model',
				parent: 'sedadla_auto_znacka',
			},
		};
		let group = State.product_group_id();
		if (!combinations.hasOwnProperty(group)) {
			return;
		}
		if (field_id !== combinations[group].parent) {
			return;
		}
		if (label === undefined) {
			console.log('label is undefined, dunno why...');
			return;
		}
		let ematch = encodeURIComponent(label.trim());
		Reference_table.auto_model_table(ematch).then(
			(response) => {
				if (response.status !== 200) {
					return;
				}
				let data = response.result.data;
				let options = {};
				for (let order in data) { if (!data.hasOwnProperty(order)) { continue; }
					options[order] = {
						[data[order].id]: data[order].model,
					};
				}
				// Dynamic_option_tables_state().set_table_options(combinations[group].child, options);
			}
		);
	}


	/**
	 * havarijne_vybava
	*/
	havarijne_vybava(field_id)
	{
		if (State.product_group_id() !== 2) {
			return;
		}
		let main_id = 'havarijne_auto_vybava';
		let with_date = 'havarijne_auto_registracia';
		let param_array = [];
		// init onchange only on watched inputs
		var watched = [
			'havarijne_auto_znacka',
			'havarijne_auto_model',
			'havarijne_objem',
			'havarijne_vykon',
			'havarijne_palivo',
			'havarijne_hmotnost',
			'havarijne_rokvyroby',
			'havarijne_auto_prevodovka',
			'havarijne_auto_pocet_sedadiel',
			'havarijne_auto_karoseria',
		];
		if (watched.indexOf(field_id) === -1) {
			return;
		}
		// exit the method if required is empty
		let required = [
			'havarijne_auto_znacka',
			'havarijne_auto_model',
			'havarijne_objem',
			'havarijne_vykon',
			'havarijne_palivo',
		];
		var i, len = required.length;
		for (i = 0; i < len; i++) {
			let value = STATE_params().get_user_input(required[i]);
			if (value === '' || value === undefined) {
				return;
			}
			param_array.push(required[i] + '=' + value);
		}
		// skip value if optional is not set
		let optional = [
			'havarijne_hmotnost',
			'havarijne_rokvyroby',
			'havarijne_auto_prevodovka',
			'havarijne_auto_registracia',
			'havarijne_auto_pocet_sedadiel',
			'havarijne_auto_karoseria',
		];
		var i, len = optional.length;
		for (i = 0; i < len; i++) {
			let value = STATE_params().get_user_input(optional[i]);
			if (value === '' || value === undefined) {
				continue;
			}
			if (optional[i] === with_date) {
				value = Functions.format_car_request_date(value);
			}
			param_array.push(optional[i] + '=' + value);
		}
		// request data and fill options
		Search_requests.car_valuation_options(param_array).then(
			(response) => {
				if (response.status !== 200) {
					return;
				}
				let data = response.result.data;
				let options = {};
				for (let order in data) { if (!data.hasOwnProperty(order)) { continue; }
					options[order] = {
						[data[order].havarijne_auto_vybava]: data[order].label_havarijne_auto_vybava,
					};
				}
				// Dynamic_option_tables_state().set_table_options(main_id, options);
			}
		);
	}


	/**
	 * havarijne_cena
	*/
	havarijne_cena(field_id)
	{
		let vybava = 'havarijne_auto_vybava'; // required, listener
		let cena = 'havarijne_auto_cena'; // required, changing
		let vyroba = 'havarijne_rokvyroby'; // required
		let with_date = 'havarijne_auto_registracia'; // formated date
		if (field_id !== vybava) {
			return;
		}
		let vybava_value = STATE_params().get_user_input(vybava);
		let vyroba_value = STATE_params().get_user_input(vyroba);
		if (vybava_value === '' || vybava_value === undefined || vyroba_value === '' || vyroba_value === undefined) {
			return;
		}
		let param_array = [];
		// required
		let required = [
			'havarijne_auto_znacka',
			'havarijne_auto_model',
			'havarijne_objem',
			'havarijne_vykon',
			'havarijne_palivo',
		];
		var i, len = required.length;
		for (i = 0; i < len; i++) {
			let value = STATE_params().get_user_input(required[i]);
			param_array.push(required[i] + '=' + value);
		}
		// optional
		let optional = [
			'havarijne_hmotnost',
			'havarijne_rokvyroby',
			'havarijne_auto_prevodovka',
			'havarijne_auto_registracia',
			'havarijne_auto_pocet_sedadiel',
			'havarijne_auto_karoseria',
		];
		var i, len = optional.length;
		for (i = 0; i < len; i++) {
			let value = STATE_params().get_user_input(optional[i]);
			if (value === '' || value === undefined) {
				continue;
			}
			if (optional[i] === with_date) {
				value = Functions.format_car_request_date(value);
			}
			param_array.push(optional[i] + '=' + value);
		}
		// evergreen
		param_array.push(vybava + '=' + vybava_value);
		// request and fill price
		Search_requests.car_valuation_price(param_array).then(
			(response) => {
				if (response.status !== 200) {
					return;
				}
				STATE_params().set_user_input(cena, response.result.data?.[0]?.[cena]);
			}
		);
	}
}

export default new Special_input_behaviour;
