<template>
	<select
		:id="field_id"
		:name="field_id"
		v-model="value"
		:disabled="field_data.disabled"
		class="form-control"
	>
		<option
			v-for="(item, key) in field_data.enum"
			:value="option_value(item)"
		>
			{{ option_label(item) }}
		</option>
	</select>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import Reference_table from '@/logic/requests/Reference_table.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			emit_name: String, // or undefined
		},
		components: {
		},
		emits: ['validate_global_changed', 'recalculate_product'],
		data() {
			return {
				dynamic: {
					auto_model: 'auto_znacka',
					havarijne_auto_model: 'havarijne_auto_znacka',
					gap_auto_model: 'gap_auto_znacka',
					sedadla_auto_model: 'sedadla_auto_znacka',
				},
			}
		},
		created() {
			this.request_dynamic_value();
		},
		methods: {
			// we have two types of enum options
			// one straight with key=>string (rokvyroby), other also with useless category data key=>object (auto_znacka)
			option_value(item)
			{
				let key = Object.keys(item)[0];
				let value = item[key];
				if (typeof value === 'string') {
					return key;
				} else if (typeof value === 'object') {
					return value.value;
				}
			},
			option_label(item)
			{
				let key = Object.keys(item)[0];
				let value = item[key];
				if (typeof value === 'string') {
					return value;
				} else if (typeof value === 'object') {
					return value.label;
				}
			},
			request_dynamic_value()
			{
				if (this.cached_dynamic_parent === null) {
					return;
				}
				// find label
				Reference_table.auto_model_table(this.dynamic[this.field_id], this.cached_dynamic_parent).then(
					(response) => {
						if (response.status !== 200) {
							return;
						}
						let data = response.result.data;
						let options = {};
						for (let order in data) { if (!data.hasOwnProperty(order)) { continue; }
							options[order] = {
								[data[order].id]: data[order].model,
							};
						}
						this.field_data.enum = options;
					}
				);
			},
		},
		computed: {
			cached_dynamic_parent()
			{
				if (!this.dynamic.hasOwnProperty(this.field_id)) {
					return null;
				}
				return STATE_params().get_user_input(this.dynamic[this.field_id]);
			},
			value:
			{
				get() {
					return STATE_params().get_user_input(this.field_id);
				},
				set(event_value) {
					STATE_params().set_user_input(this.field_id, event_value);
					if (this.emit_name !== undefined) {
						this.$emit(this.emit_name);
					}
				},
			},
		},
		watch: {
			cached_dynamic_parent()
			{
				this.request_dynamic_value();
			},
		},
	}
</script>
