import { createStore } from 'vuex'

export default createStore({
	state: {
		workflow_status: {
			actions: [],
			finished: false,
			internal: false,
			step: -1,
			stepState: 'before_init',
		},
		form_storage: {},
		form_errors: {},
		product_detail: {},
	},
	mutations: {
		workflow_status(state, payload) {
			state.workflow_status = payload.value;
		},
		form_storage(state, payload) {
			state.form_storage = payload.value;
		},
		form_errors(state, payload) {
			state.form_errors = payload.value;
		},
		product_detail(state, payload) {
			state.product_detail = payload.value;
		},
	},
	getters: {
	},
	actions: {
	},
	modules: {
	}
})
