<template>
	<div
		:id="product_id"
		class="row vysledky-produkt product"
	>
		<!-- stlpec s udajmi -->
		<div class="col-lg-10 col-md-12 col-sm-12 content">
			<!-- promo -->
			<span v-show="p_data.product.promo === '1' && p_data.product.promo === '3'" class="odporucany-produkt"></span>
			<div v-show="p_data.product.promo === '1' && p_data.product.promo === '2'" class="ribbon"><span>NÁŠ TIP</span></div>
			<div class="vysledky-produkt-top top">
				<div class="row">
					<!-- logo -->
					<div class="col-12 col-lg-2 product-logo">
						<div class="logo-wrapper">
							<img :src="p_data.logo">
						</div>
					</div>
					<div class="col-12 col-lg-4 text-center text-lg-left">
						<!-- title -->
						<h4 class="product-name">
							{{ '#' + product_id + ' ' + product_name }}
						</h4>
						<!-- stars -->
						<div class="product-rating text-nowrap">
							<span
								v-for="index in 5"
								:class="['star', index > p_data.product.stars ? 'star-empty' : '']"
							></span>
						</div>
						<!-- spoluucast, active on HP (and PZP?) -->
						<p class="spoluucast_container">
							<span class="spoluucast_plain_text">{{ p_data.product.spoluucast }}</span>
							<span class="spoluucast_dropdown"></span>
						</p>
						<div class="detail-compare text-nowrap">
							<!-- detail modal trigger-->
							<span class="detail-link">
								<button v-show="p_data.product.detail === '2'" @click="open_product_detail" class="detail-modal" >
									<span class="i_detail_modal"></span>detail
								</button>
								<!-- graph trigger -->
								<!-- <a href="#" class="detail-modal hidden" data-toggle="modal" data-target="detail" product-graph_button="">
									<span class="i_detail_modal"></span>GRAF
								</a> -->
							</span>
						</div>
					</div>
					<!-- icons -->
					<div class="col-lg-6 col-md-12 col-sm-12">
						<ul class="rychly-prehlad">
							<li
								v-for="icon in p_data.icons"
								:class="icon.class"
							>
								{{ icon.title }}
								<span
									v-if="icon.tooltip !== undefined"
									class="ttip-inline"
									:data-original-title="icon.tooltip"
									data-toggle="tooltip"
									data-placement="top"
								>
									(€)
								</span>
							</li>
						</ul>
					</div>
				</div>
			</div>
			<div class="linka"></div>
			<!-- pod ciarou -->
			<div class="vysledky-produkt-bottom">
				<div class="row">
					<div class="col-lg-6">
						<!-- benefits -->
						<ul class="zahrna-nezahrna">
							<li
								v-for="benefit in p_data.benefits"
								:class="benefit.class"
							>
								{{ benefit.title }}
							</li>
						</ul>
					</div>
					<div class="col-lg-6">
						<!-- product form -->
						<ul class="moznost-pripoistenia">
							<li
								v-for="(field_data, field_id) in p_data.inputs"
								class="zahrna"
							>
								<Checkbox
									v-if="field_data.type === 'checkbox'"
									:field_id="field_id"
									:field_data="field_data"
									emit_name="recalculate_product"
									@recalculate_product="recalculate_product"
								/>
								<Select
									v-if="field_data.type === 'select'"
									:field_id="field_id"
									:field_data="field_data"
									emit_name="recalculate_product"
									@recalculate_product="recalculate_product"
								/>
								<Date
									v-if="field_data.type === 'date'"
									:field_id="field_id"
									:field_data="field_data"
									emit_name="recalculate_product"
									@recalculate_product="recalculate_product"
								/>
							</li>
						</ul>
					</div>
				</div>
				<div class="row">
					<div class="row">
						<div class="col-md-12 ">
							<div
								v-if="p_data.product.crossell"
								class="produkt-upsell"
							>
								<span class="produkt-upsell-maskot"></span>
								<span>
									{{ p_data.product.crossell }}
								</span>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<!-- stlpec s cenou -->
		<div class="col-lg-2 col-md-12 col-sm-12 vysledky-produkt-cena">
			<!-- dont know if this discounts are even used -->
			<div class="cena-wrapper cena-wrapper-left">
				<span
					v-if="p_data.price.base > 0"
					title="Základná cena pred zľavami"
					class="cena-povodna hidden"
				>
					{{ p_data.price.base }}€
				</span>
				<span
					v-if="p_data.price.discount > 0"
					title="Celková zľava"
					class="cenovka-mensia hidden"
				>
					{{ p_data.price.discount }}%
				</span>
			</div>
			<!-- price -->
			<div
				v-show="!loading"
				class="cenaCell cena-wrapper cena-wrapper-left"
			>
				<span class="cena">{{ p_data.price.for_period }}</span>
				<span class="currency">{{ p_data.price.currency }}</span>
				<span class="perioda">{{ p_data.price.period }}</span>
			</div>
			<!-- loading -->
			<div
				v-show="loading"
				class="cenaCell"
			>
				<div class="spinner">
					<div class="bounce1"></div>
					<div class="bounce2"></div>
					<div class="bounce3"></div>
				</div>
			</div>
			<!-- product select button -->
			<div
				v-show="!loading"
				class="vybratDiv cena-wrapper cena-wrapper-right"
			>
				<button
					v-if="p_data.button.show === '1'"
					@click="select_this_product()"
					class="btn btn-accent btn-right btn-xlg"
				>
					Vybrať
				</button>
				<span
					v-if="p_data.error.info"
					class="nemoznevybrat"
				>
					<span
						v-if="p_data.error.alt_text"
						class="viacinfo"
					>
						VIAC INFO
					</span>
					<span
						v-else
					>
						NEDÁ SA POISTIŤ
					</span>
					<a
						:data-original-title="p_data.error.info"
						class="ttip"
						data-toggle="tooltip"
						data-placement="top"
					>
						?
					</a>
				</span>
			</div>
		</div>
	</div>
</template>

<script>
	import State from '@/store/State.js';
	import STATE_core from '@/store/STATE_core.js';
	import Product_requests from '@/logic/requests/Product_requests.js';
	import Product_helpers from '@/components/form_app/product/Product_helpers.js';
	import Checkbox from '@/components/forms/basic_inputs/Checkbox.vue';
	import Select from '@/components/forms/basic_inputs/Select.vue';
	import Date from '@/components/forms/advanced_inputs/Date.vue';
	import Functions from '@/logic/Functions.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
			product_id: String,
			product_name: String,
			response: Object,
		},
		components: {
			Checkbox,
			Select,
			Date,
		},
		emits: ['recalculate_product'],
		data() {
			return {
				p_data: {
					logo: '',
					error: {
						info: '',
						alt_text: false,
					},
					button: {
						show: '0',
						next_form: '',
						next_contract: '',
					},
					price: {
						for_period: '',
						currency: '',
						period: '',
						base: '',
						discount: '',
					},
					product: {
						promo: '',
						stars: 0,
						spoluucast: '',
						detail: '',
						detail_template: '',
						crossell: '',
					},
					icons: [
						{
							title: '',
							tooltip: '',
							order: 0,
							class: '',
						},
					],
					benefits: [
						{
							title: '',
							class: '',
						},
					],
					inputs: {
						// kooperativa_krytie: {
						// 	type: 'select',
						// 	enum: {...},
						// 	type_settings: {...},
						// },
						// ...
					},
				},
				loading: true,
			}
		},
		created() {
			this.asign_values();
		},
		watch: {
			response()
			{
				this.asign_values();
			},
		},
		methods: {
			asign_values()
			{
				if (typeof this.response !== 'object') {
					// reset this.p_data to original state when recalculate happens
					Object.assign(this.$data, this.$options.data());
					return;
				}
				// result inside product result
				let result = this.response.result[this.product_id].result;
				// frekvenciaNP
				let period = Product_helpers.find_period(this.response);
				// product data
				this.p_data = {
					logo: result?.head?.logo?.comment,
					period: period,
					error: {
						info: Product_helpers.error_tooltip(result),
						alt_text: Product_helpers.check_alt_text(),
					},
					price: {
						for_period: Product_helpers.price_for_period(result, period),
						currency: result?.result?.currency?.result || '€',
						period: Product_helpers.label_for_period(result, period),
						base: result?.result?.base?.result,
						discount: result?.result?.discount?.result,
					},
					button: {
						show: result?.next?.next?.result,
						next_form: result?.next?.next_form?.result,
						next_contract: result?.next?.next_contract_html?.result,
					},
					product: {
						promo: result?.product?.promo?.result,
						stars: parseInt(result?.product?.stars?.result, 10),
						spoluucast: result?.product?.spoluucast?.formula_name, // toto je plaintext, treba doriesit este select
						detail: result?.product?.detail?.result,
						detail_template: result?.product?.detail?.comment,
						crossell: result?.product?.crossell.result === '1' ? result?.product?.crossell?.comment : '',
					},
					icons: Product_helpers.sort_icons(result?.icon),
					benefits: Product_helpers.sort_benefits(result?.benefit),
					inputs: Product_helpers.product_form_formatter(result?.results_form?.comment, true),
				};
				this.loading = false;
			},
			select_this_product() {
				this.loading = true;
				// store selected product for later usage
				STATE_params().set_selected_product(
					this.product_id,
					this.p_data.button.next_form,
					this.p_data.button.next_contract,
					this.p_data.period
				);
				// select request and redirect
				Product_requests.select_product().then(
					(response) => {
						if (response.result.response !== 'success') {
							return;
						}
						STATE_core().change_step(3);
					}
				);
			},
			open_product_detail()
			{
				// getting documents actually requires selected product id
				STATE_params().set_selected_product(
					this.product_id,
					null,
					null,
					null
				);
				// modal needs some info for gethtml request
				State.set_product_detail(this.product_name, this.p_data.product.detail_template);
				// open modal, request starts on created()
				STATE_modals().open_it('product_detail');
			},
			recalculate_product()
			{
				// take form data and launch calculate from step2
				let form_data = Functions.serialize_specific_inputs(Object.keys(this.p_data.inputs));
				this.$emit('recalculate_product', this.product_id, form_data);
			},
		},
		computed: {
		},
	}
</script>
